h2{
    text-align: center;
    color: darkblue;
}

body{
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    background-color: gainsboro;
}

input{
    width: 30em;
    padding: .2em;
    margin-bottom: 1em;
}

input, label{
    display: block;
    color: blue;
}

.card{
    font-size: 12px;
    box-shadow: 0.2em 0 0 0.2em gray;
    transition: 1s;
}
.card:hover{
    box-shadow: 0 0 2em 0.2em gray;
    transition: 1s;
}

button.btn.btn-primary.invNuevo {
    border-radius: 100%;
    position: fixed;
    right: 0.5em;
    bottom: 0.5em;
    font-size: 30px;
    box-shadow: 0 0.1em 0.1em 0 darkblue ;
    transition: 0.5s;
    width: 1.9em;
    height: 1.9em;
}

button.btn.btn-primary.invNuevo:hover{
    box-shadow: 0 0.2em 0.2em 0 darkblue ;
    transition: 1s;
}

td.botones button{
    margin: 10px;
}

.table{
    display: flexbox;
    height: 5px;
}

tr td{
    
}


.nuevoInventario{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: gainsboro;
}


.nuevoInventario-header{
    display: flex;
    justify-content: space-between;
    margin: 2em;
}

.btn.btn-secondary.cerrarInventario{
    border-radius: 0.5em;
    background-color: cornsilk;
    color: black;
}

.invNuevo{
    width: 10em;
}


button.btn.btn-primary{
    width: 10em;
    background-color:blue;
    margin: 10px;
}

div.row{
    width: 100%;
}

div.botones{
    text-align: center;
    margin: 10px;
}


select:valid, input:valid {
    border-color: green;
    color: black;
}

select:invalid, input:invalid{
    color: red;
    border-color: red;
}

img{
    margin: 0 auto;
    height: 15em;
    width: 20em;
}

button.btn.btn-danger{
    display: inline-block;
    position: fixed;
    top: 80px;
    right: 20px;
    width: 10em;
}